import React from 'react';
import { Link } from 'react-router-dom';
import korotaeva from "../images/_viber_2022-02-01_21-57-12-418.jpg"

function Main({ item, isOpen }) {

    function handleClick(e) {
        isOpen(true);
        const target = e.target;
        return item({
            name: target.alt,
            link: target.src,
        })
    }
    return (
        <main className="content">
            <section className="greetings">
                <h2 className="greetings__title">,  , !</h2>
                <h2 className="greetings__title">      !</h2>
                <div className="greetings__container">
                    <div className="greetings__image-container">
                        <img className="greetings__image" onClick={handleClick} src={korotaeva}
                            alt="  -  ." />
                    </div>
                    <div className="greetings__container-text">
                        <p className="greetings__text">    ,    .
                                    , .
                                     ,  
                               ,   
                            .</p>
                        <p className="greetings__text">    -  -
                            
                              c ,    .</p>
                        <p className="greetings__text">    !</p>
                        <p className="greetings__text">       " !"</p>
                        <div className="header__button-container">
                            <button className="header__button-esse"><Link to="/esse" className="header__button"></Link></button>
                        </div>
                    </div>
                </div>
            </section>
        </main>

    );
}

export default Main;
